import {ListaPilotos} from '../models/ListaPilotos';
import {PilotoService} from '../services/PilotoService';
import {Piloto} from '../models/Piloto';

export class PilotoController {
    constructor() {
        this._listaPilotos = new ListaPilotos();
            
        this._service = new PilotoService();

        this._init();
    }

    _init() {
       let pilotos = [
            {codigo: '002', nome: 'K.RAIKKONEN'},
            {codigo: '011', nome: 'S.VETTEL'},
            {codigo: '015', nome: 'F.ALONSO'},
            {codigo: '023', nome: 'M.WEBBER'},
            {codigo: '033', nome: 'R.BARRICHELLO'},
            {codigo: '038', nome: 'F.MASSA'}
        ];
                    
        //pilotos.forEach(piloto => this._service.cadastra(piloto));
        pilotos.forEach(piloto => this._listaPilotos.adiciona(piloto));
    }

    listaPilotos() {
        return this._listaPilotos;
    }    
}

let pilotoController = new PilotoController();

export function pilotoCurrentInstance() {
    return pilotoController;
}
